.btn-loadMore__container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-loadMore__item {
  opacity: 1;
  visibility: visible;
  margin: 20px;
  padding: 10px 40px;
  border: 1px solid $accent-color;
  border-radius: 8px;
  background-color: $accent-color;
}

.btn-loadMore__item.is-hidden {
  display: none;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.btn-loadMore__item-trend {
  opacity: 1;
  visibility: visible;
  margin: 20px;
  padding: 10px 40px;
  border: 1px solid $accent-color;
  border-radius: 8px;
  color: $light-text-color;
  background-color: $accent-color;

  &:hover,
  &:focus {
    box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
    cursor: pointer;
  }
}

.btn-loadMore__item-trend.is-hidden {
  display: none;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.tui-pagination.is-hidden {
  opacity: 0;
  visibility: hidden;
}
