:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

body {
  transition: color 150ms var(--timing-function), background-color 150ms var(--timing-function);
}

body.light-theme {
  background-color: $light-background-color;
  color: #333333;
}

.dark-theme {
  background-color: #333333;
  color: $light-background-color;
}

header {
  position: relative;
}

.toolbar {
  display: flex;
  justify-content: flex-end;
  padding: 5px;
  position: absolute;
  top: 180px;
  right: 5px;
}

.theme-switch {
  display: flex;
  align-items: center;
}

.theme-switch__icon {
  color: $accent-color;
  height: 24px;
  width: 24px;
  margin-right: 8px;
  margin-left: 8px;
}

.theme-switch__control {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: relative;
  width: 50px;
  height: 25px;
  border-radius: 50em;
  padding: 3px;
}

.theme-switch__toggle {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
}

.theme-switch__track {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: transparent;
  user-select: none;
  background-color: #333;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
}

.theme-switch__marker {
  position: relative;
  background-color: $accent-color;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  z-index: 2;
  pointer-events: none;
  box-shadow: 0 1px 1px $dark-background-color;
  transform: translateX(3px);
  transition: transform 250ms var(--timing-function);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #d3d3d4;
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(25px);
}

.theme-switch__toggle:focus + .theme-switch__track,
.theme-switch__toggle:active + .theme-switch__track {
  box-shadow: 0 0 0 2px $light-background-color;
}

.shown {
  display: block;
}

.notshown {
  display: none;
}
